:root {
  --primary-color: #5B87EF;
  --title-color: #3d4879;
  --text-color: #83849c;
}

* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}

body {
  background-color: #f0f0f5;
  -webkit-font-smoothing: antialiased;
  color: var(--text-color);
}

button {
  cursor: pointer;
}

input {
  height: 60px;
  width: 100%;
  border: 0;
  border-radius: 8px;
  margin-bottom: 8px;
  padding: 0 24px;
  font-size: 16px;
  color: var(--text-color);
}

input::placeholder {
  color: #bbb;
}

body, input, button, textarea {
  font-family: Roboto, Helvetica, sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  color: var(--title-color);
  font-family: Montserrat, sans-serif;
}

p, span {
  font-family: Roboto, sans-serif;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}
