.data-page {
  max-width: 1600;
  width: 100%;
}

.data-page form {
  width: 100%;
  margin-top: 60px;
  display: flex;
  flex-direction: column;
}

.data-page form .input-control {
  margin-bottom: 16px;
}

.data-page form .input-control label {
  font-size: 18px;
}

.data-page form .input-control input {
  background-color: #f0f0f5;
  height: 60px;
  margin-top: 8px;
  font-size: 18px;
}

.data-page form button[type="submit"] {
  height: 60px;
  max-width: 300px;
  margin-top: 24px;
  width: 100%;
  background-color: var(--primary-color);
  color: #fff;
  font-size: 18px;
  border-radius: 8px;
  border: 0;
  align-self: flex-end;
  transition: all .3s;
}

.data-page form button[type="submit"]:hover {
  opacity: .8;
}